<script>
  import echarts from 'echarts'

export default {

  data() {
    return {
      formLayout: 'horizontal',
      form: this.$form.createForm(this),
      Name: [],
      value: 3
    }
  },

  name: 'HelloWorld',
  props: {
    msg: String,
    type:{
      validator: (value)=>{
        return typeof value === 'string';
      }

    }

  },
  methods: {
    onChange() {
      console.log(value);
    },

    getUsers(){
      axios.get("/users/api").then(
        response => {
          console.log(response.data);
        }
      )
      .catch(error=>{
        alert(error);
      })
    },

    handleSubmit(e){
      e.preventDefault();
      this.form.validateFields((err, values) => {
        if (!err) {
          console.log('Received values of form: ', values);
          axios.post("/users/api",values).then(
            response =>{
              console.log(response.data);

              this.$notification.open({
                message: '通知',
                description: '您的信息已添加成功',
                onClick: () => {
                  console.log('Notification Clicked!');
                },
              });

            }
          )
            .catch(error =>{
                new Error(error)
              }
            )

        }
      });
    }

  },

  mounted(){
    var myChart = echarts.init(document.getElementById('main'));

    // 指定图表的配置项和数据
    var option = {
      title: {
        text: 'ECharts 入门示例'
      },
      tooltip: {},
      legend: {
        data:['销量']
      },
      xAxis: {
        data: ["衬衫","羊毛衫","雪纺衫","裤子","高跟鞋","袜子"]
      },
      yAxis: {},
      series: [{
        name: '销量',
        type: 'bar',
        data: [5, 20, 36, 10, 10, 20]
      }]
    };

    // 使用刚指定的配置项和数据显示图表。
    myChart.setOption(option);

    console.log("process.env.NODE_ENV: ",process.env.NODE_ENV);
  }
}
</script>

